@import url(https://fonts.googleapis.com/css2?family=Do+Hyeon&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&family=Rubik:ital@1&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.recipe_recipe__3kb72{
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(71, 71, 71);
    margin: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: rgb(255 240 251);
    align-items: center;
    padding: 5px;
}
.recipe_img__2swQC{
    border-radius: 10%;
}
.App {
  text-align: center;
  min-height:100vh;
  padding: 5px 10px;
  background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
}
.logo{
  font-family: 'Do Hyeon', sans-serif;
  letter-spacing: 3px;
  color: #532caf;
}
.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar{
  width: 50%;
  border: none;
  padding: 5px;
  margin: 0px 5px;
  border-radius: 6px;
}
.search-btn{
  background-image: linear-gradient(to top, #fdcbf1 0%, #fdcbf1 1%, #e6dee9 100%);
  padding: 5px;
  margin: 0px 5px;
  border-radius: 6px;
  color:black;
  border: 1px solid rgba(0, 0, 0, 0.342);
  transition: 0.7s;
  font-weight: 400;
  font-family: 'Roboto', sans-serif;
}
.search-btn:hover{
  background: white;
  color: #6e48c7;
  border-color: #6e48c7;
}
.recipies{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
li{
  max-width: 400px;
  color: #6e48c7;
  text-align-last: start;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
}
h1,h5{
  color: #532caf;
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
}
p.logo-des {
  color: #532caf; 
  font-family: 'Rubik', sans-serif;
}
.line {
  width: 20px;
  color: black;
  height: 3px;
  background-color:  #532caf; 
  margin: 0px 4px;
}
.logobottom{
  display:flex;
  justify-content: center;
  align-items: center;
  position: relative;
  top: -38px;
}
